import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

//     
const NUM_CIRCLES = 6;
const SCREEN_WIDTH = 800;
const SCREEN_HEIGHT = 300;
const CIRCLE_WIDTH = 20;

function AimingGame() {
  const [circles, setCircles] = useState(generateCircles());
  const [end, setEnd] = useState(false);
  const navigate = useNavigate();


  //    
  function generateCircles() {
    const newCircles = [];
    for (let i = 0; i < NUM_CIRCLES; i++) {
      newCircles.push({
        id: i,
        x: Math.random() * (SCREEN_WIDTH - 50), //     x
        y: Math.random() * (SCREEN_HEIGHT - 50) //     y
      });
    }
    return newCircles;
  }

  useEffect(() => {
    if (circles.length === 0) {
      setEnd(true);
      setTimeout(() => {
        navigate('/ReturnZero/different-letter-game');
      }, 1000);
    }
  },[circles, navigate])

  //   
  const handleCircleClick = (id) => {
    setCircles((prevCircles) =>
      prevCircles.filter((circle) => circle.id !== id)
    );
  };

  return (
    <div className={`column-align fadein ${end ? 'fadeout' : ''}`}>
      <h2 className="game-title">Aiming Game</h2>
      <p className="game-desc">
            !    !
      </p>
      <div
        onClick={() => setCircles(generateCircles())}
        style={{
          position: "relative",
          width: `${SCREEN_WIDTH}px`,
          height: `${SCREEN_HEIGHT}px`,
          margin: "0 auto",
          background: "#161616",
          borderRadius: "30px"
        }}
      >
        {circles.map((circle) => (
          <div
            key={circle.id}
            onClick={(e) => {
              e.stopPropagation(); //   
              handleCircleClick(circle.id);
            }}
            style={{
              position: "absolute",
              top: `${circle.y}px`,
              left: `${circle.x}px`,
              width: `${CIRCLE_WIDTH}px`,
              height: `${CIRCLE_WIDTH}px`,
              backgroundColor: "#f0323c",
              borderRadius: "50%",
              cursor: "pointer"
            }}
          ></div>
        ))}
      </div>
    </div>
  );
}

export default AimingGame;
