import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

//   
const LEVELS = [
  { target: "", options: "" },
  { target: "", options: "" },
  { target: "", options: "" }
];

function DifferentLetterGame( { stopTimer, elapsedTime } ) {
  const [currentLevel, setCurrentLevel] = useState(1); //  
  const gridSize = currentLevel + 3;
  const [grid, setGrid] = useState([]); //    
  const [targetIndex, setTargetIndex] = useState(null); //   
  const [end, setEnd] = useState(false);
  const navigate = useNavigate();

  //    
  const generateGrid = () => {
    console.log(currentLevel);
    const { target, options } = LEVELS[currentLevel-1];
    const totalCards = gridSize * gridSize;
    const targetIdx = Math.floor(Math.random() * totalCards); //   
    const newGrid = Array.from({ length: totalCards }, (_, i) =>
      i === targetIdx ? target : options
    );

    setGrid(newGrid);
    setTargetIndex(targetIdx);
  };

  //      
  const startNextLevel = () => {
    if (currentLevel < LEVELS.length) {
      
      setCurrentLevel((prev) => prev + 1);
    } else { // currentLevel  result 
      setEnd(true);
      setTimeout(() => {
        stopTimer();
        navigate("/ReturnZero/result", { state: { time: elapsedTime } });
      }, 1000);
    }
  };

  // currentLevel     generateGrid()
  useEffect(() => {
    if (currentLevel <= LEVELS.length) {
      generateGrid();
    }
  }, [currentLevel]);

  //   
  const handleCardClick = (index) => {
    if (index === targetIndex) {
      startNextLevel(); //   
    } else {
    }
  };

  return (
    <div className={`column-align fadein ${end ? "fadeout" : ""}`}>
      <h2 className="game-title">   </h2>
      <p className="game-desc">     ! ({currentLevel}/{LEVELS.length})</p>
      <div
        style={{
          display: "grid",
          gridTemplateColumns: `repeat(${gridSize}, 1fr)`,
          gap: "10px",
          width: "350px",
          height: "350px",
          maxWidth: "400px",
          margin: "20px auto"
        }}
      >
        {grid.map((char, index) => (
          <button
            key={index}
            onClick={() => handleCardClick(index)}
            style={{
              fontSize: "20px",
              padding: "10px",
              backgroundColor: "#9eebcb",
              border: "none",
              cursor: "pointer",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              fontFamily: "Noto Sans KR",
              fontWeight: "500",
              color: "#161616"
            }}
          >
            {char}
          </button>
        ))}
      </div>
    </div>
  );
}

export default DifferentLetterGame;
